}

.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.form__container {
  margin-top: 50px;
}

.weather__container {
  width: 340px;
  margin: 30px auto;
  -webkit-box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
          box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  color: #4a4a4a;
  max-width: 100%;
  position: relative;
  background-color: rgb(234, 235, 237);
}



